import { MapsAPILoader } from '@agm/core';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';
import { CoordonneesService } from '../services/coordonnees.service';

@Component({
  selector: 'app-page-un',
  templateUrl: './page-un.component.html',
  styleUrls: ['./page-un.component.css']
})
export class PageUnComponent implements OnInit {

  @ViewChild('search') public searchElementRef: ElementRef;

  dataSub: Subscription;

  data: any;

  indiv: boolean; // indiv, prop, typeBat : Stockage local en attendant le composant suivant
  prop: boolean;
  geoCoder: google.maps.Geocoder;

  /* typeBat : Identifiant du type de bâtiment
  1 : Logement collectif | 2 : Santé | 3 : Groupe scolaire | 4 : Complexe sportif
  5 : Hôtel/Camping | 6 : Bâtiment industriel | 7 : Exploitation agricole | 8 : Bâtiment tertiaire
  */
  typeBat: number;

  constructor(private mapsAPILoader: MapsAPILoader, private coordonneesService: CoordonneesService) {
    this.data = {};
  }

  ngOnInit() {
    this.dataSub = this.coordonneesService.dataSubject.subscribe(
      (data) => {
        this.data = data;
      }
    );

    this.coordonneesService.emitDataSubject();

    this.data['indiv'] !== undefined ? this.indiv = this.data['indiv'] : this.indiv = true;
    this.data['prop'] !== undefined ? this.prop = this.data['prop'] : this.prop = true;
    this.data['typeBat'] !== undefined ? this.typeBat = this.data['typeBat'] : this.typeBat = 1;

    this.mapsAPILoader.load().then(() => {
      this.geoCoder = new google.maps.Geocoder;
      const autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement);
      autocomplete.addListener("place_changed", () => {
        this.coordonneesService.setPos(autocomplete);
      });
    });
  }

  addData(key: string, value: any) {
    this.coordonneesService.addData(key, value);
  }


}
