import { MapsAPILoader } from '@agm/core';
import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/internal/Subscription';
import { CoordonneesService } from '../services/coordonnees.service';

@Component({
  selector: 'app-google-places',
  templateUrl: './google-places.component.html',
  styleUrls: ['./google-places.component.css']
})
export class GooglePlacesComponent implements OnInit {

  dataSub: Subscription;

  data: any;

  zoom: number;

  constructor(private mapsAPILoader: MapsAPILoader, private coordonneesService: CoordonneesService) {
    this.data = {};
    this.zoom = 20;
  }

  ngOnInit() {
    this.dataSub = this.coordonneesService.dataSubject.subscribe(
      (data) => {
        this.data = data;
      }
    );
    this.coordonneesService.emitDataSubject();
  }
}
