import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PageUnComponent } from './page-un/page-un.component';
import { GooglePlacesComponent } from './google-places/google-places.component';
import { PageDeuxComponent } from './page-deux/page-deux.component';

const routes: Routes = [
  { path: 'pageun', component: PageUnComponent },
  { path: 'pagedeux', component: PageDeuxComponent },
  { path: 'gmaps', component: GooglePlacesComponent },
  { path: '**', component: PageUnComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
